body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
body {
  font-family: 'Varela', sans-serif;
  display: block !important;
  max-width: 100vw;
  position: relative;
}

.button {
  float: left;
  margin-top: 40px;
  position: relative;
  z-index: 9999;
  color: green;
  background-color: rgba(255, 0, 0, 0);
  border: none !important;
  cursor: pointer;
}

button:focus {outline:0 !important;}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 50px 50px;
}

.continue {
  color: red;
  opacity: 50%;
  font-size: 45px;
}

.continue-button {
  color: black;
  background-color: rgb(185, 98, 98);
}

.continue-button-center {
  color: rgb(250, 0, 0);
  background-color: rgba(0, 0, 0, 0.548);
  font-size: 24px;
  border-radius: 8px;
  padding: 13px;
  margin-left: 50%;
  margin-top: 360px;
}


.hidden {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 2s, opacity 2s linear;
}

.input {
  border-radius: 6px;
  padding: 3px;
  margin-left: 76px;
  margin-top: 13px;
}

.screenText {
  color: rgb(13, 139, 13);
  font-size: 24px;
  margin-left: 22px;
}

.text-info{
  color: white;
  font-size: 20px;
}

.tv-static {
  background-image: url(/allanyeznaian/static/media/Static.496b32c8.gif);
  opacity: 0.3;
}




