.fly {
    position: relative;
    -webkit-animation: fly 5s infinite; /* Safari 4.0 - 8.0 */
    animation: fly 5s infinite;
}
#fly {
    position: relative;
    -webkit-animation: fly 5s infinite; /* Safari 4.0 - 8.0 */
    animation: fly 2s infinite;
}

@-webkit-keyframes fly {
    0% { left: 100%; }
    70%, 100% { left: -100px; }
}
@keyframes fly {
    0% { left: 80%; }
    70%, 100% { left: -100px; }
}

